import React, { Component } from "react";
import axios from "axios";
import { saveAs } from "file-saver";
export default class AboutMe extends Component {
  state = {
    name: "",
    receiptId: 0,
    price1: 0,
    price2: 0,
  };
  handleChange = ({ target: { value, name } }) =>
    this.setState({ [name]: value });

    createAndDownloadPdf = () => {
        axios.post('https://excellent-capable-vulcanodon.glitch.me/create-pdf', this.state)
          .then(() => axios.get('https://excellent-capable-vulcanodon.glitch.me/fetch-pdf', { responseType: 'blob' }))
          .then((res) => {
            const pdfBlob = new Blob([res.data], { type: 'application/pdf' });
    
            saveAs(pdfBlob, 'newPdf.pdf');
          })
      }
  render() {
    return <div className="container-contact">Testowe drukowanie:<br/>
    <div className="App">
      <input type="text" placeholder="Name" name="name" onChange={this.handleChange}/>
      <input type="number" placeholder="Receipt ID" name="receiptId" onChange={this.handleChange} />
      <input type="number" placeholder="Price 1" name="price1" onChange={this.handleChange} />
      <input type="number" placeholder="Price 2" name="price2" onChange={this.handleChange} />
      <button onClick={this.createAndDownloadPdf}>Download PDF</button>
    </div>
    <div>
        <p>{this.state.name}</p>
        <p>{this.state.receiptId}</p>
        <p>{this.state.price1}</p>
        <p>{this.state.price2}</p>
    </div>
    </div>;
  }
}
